import React from "react";
import {
  Grid,
  Box,
  Checkbox,
  FormGroup,
  FormControlLabel,
  FormControl,
} from "@material-ui/core";

import BaseCard from "../../components/BaseCard/BaseCard";

import CheckBoxOutlineBlankIcon from "@material-ui/icons/CheckBoxOutlineBlank";
import CheckBoxIcon from "@material-ui/icons/CheckBox";
import Favorite from "@material-ui/icons/Favorite";
import FavoriteBorder from "@material-ui/icons/FavoriteBorder";

const ExCheckbox = () => {
  // 2
  const [checked, setChecked] = React.useState(true);

  const handleChange = (event) => {
    setChecked(event.target.checked);
  };
  return (
    <Box>
      <Grid container spacing={0}>
        {/* ------------------------- row 1 ------------------------- */}
        <Grid
          item
          xs={12}
          lg={4}
          sm={6}
          sx={{
            display: "flex",
            alignItems: "stretch",
          }}
        >
          <BaseCard title="Default Checkbox">
            <Box
              sx={{
                textAlign: "center",
              }}
            >
              <Checkbox
                checked={checked}
                onChange={handleChange}
                inputprops={{ "aria-label": "primary checkbox" }}
              />

              <Checkbox
                disabled
                checked
                inputprops={{ "aria-label": "disabled checked checkbox" }}
              />
              <Checkbox
                defaultChecked
                indeterminate
                inputprops={{ "aria-label": "indeterminate checkbox" }}
              />
              <Checkbox
                defaultChecked
                color="default"
                inputprops={{ "aria-label": "checkbox with default color" }}
              />
            </Box>
          </BaseCard>
        </Grid>
        {/* ------------------------- row 1 ------------------------- */}
        <Grid
          item
          xs={12}
          lg={4}
          sm={6}
          sx={{
            display: "flex",
            alignItems: "stretch",
          }}
        >
          <BaseCard title="Default with Colors">
            <Box
              sx={{
                textAlign: "center",
              }}
            >
              <Checkbox
                defaultChecked
                color="primary"
                inputprops={{ "aria-label": "checkbox with default color" }}
              />
              <Checkbox
                defaultChecked
                color="secondary"
                inputprops={{ "aria-label": "checkbox with default color" }}
              />
              <Checkbox
                defaultChecked
                sx={{
                  color: "success.main",
                  "&.Mui-checked": {
                    color: "success.main",
                  },
                }}
              />
              <Checkbox
                defaultChecked
                sx={{
                  color: "error.main",
                  "&.Mui-checked": {
                    color: "error.main",
                  },
                }}
              />
              <Checkbox
                defaultChecked
                sx={{
                  color: "warning.main",
                  "&.Mui-checked": {
                    color: "warning.main",
                  },
                }}
              />
            </Box>
          </BaseCard>
        </Grid>
        {/* ------------------------- row 1 ------------------------- */}
        <Grid
          item
          xs={12}
          lg={4}
          sm={6}
          sx={{
            display: "flex",
            alignItems: "stretch",
          }}
        >
          <BaseCard title="Sizes & Custom icon">
            <Box>
              <FormGroup
                row
                sx={{
                  display: "flex",
                  justifyContent: "center",
                }}
              >
                <FormControlLabel
                  control={
                    <Checkbox
                      icon={<CheckBoxOutlineBlankIcon />}
                      checkedIcon={<CheckBoxIcon />}
                      name="checkednormal"
                    />
                  }
                  label="Normal Size"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      icon={<CheckBoxOutlineBlankIcon fontSize="small" />}
                      checkedIcon={<CheckBoxIcon fontSize="small" />}
                      name="checkedsmall"
                    />
                  }
                  label="Small size"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      icon={<FavoriteBorder />}
                      checkedIcon={<Favorite />}
                      name="checkedH"
                    />
                  }
                  label="Heart"
                />
              </FormGroup>
            </Box>
          </BaseCard>
        </Grid>

        {/* ------------------------- row 1 ------------------------- */}
        <Grid
          item
          xs={12}
          lg={6}
          sm={6}
          sx={{
            display: "flex",
            alignItems: "stretch",
          }}
        >
          <BaseCard title="Placement">
            <FormControl
              component="fieldset"
              sx={{
                display: "flex",
                justifyContent: "center",
              }}
            >
              <FormGroup
                aria-label="position"
                row
                sx={{
                  justifyContent: "center",
                }}
              >
                <FormControlLabel
                  value="top"
                  control={<Checkbox color="primary" />}
                  label="Top"
                  labelPlacement="top"
                />
                <FormControlLabel
                  value="start"
                  control={<Checkbox color="primary" />}
                  label="Start"
                  labelPlacement="start"
                />
                <FormControlLabel
                  value="bottom"
                  control={<Checkbox color="primary" />}
                  label="Bottom"
                  labelPlacement="bottom"
                />
                <FormControlLabel
                  value="end"
                  control={<Checkbox color="primary" />}
                  label="End"
                  labelPlacement="end"
                />
              </FormGroup>
            </FormControl>
          </BaseCard>
        </Grid>

        {/* ------------------------- row 1 ------------------------- */}
        <Grid
          item
          xs={12}
          lg={6}
          sm={6}
          sx={{
            display: "flex",
            alignItems: "stretch",
          }}
        >
          <BaseCard title="Color with Label">
            <Box
              sx={{
                textAlign: "center",
              }}
            >
              <FormControlLabel
                value="end"
                control={<Checkbox color="primary" checked />}
                label="Primary"
                labelPlacement="end"
              />
              <FormControlLabel
                value="end"
                control={<Checkbox color="secondary" checked />}
                label="Secondary"
                labelPlacement="end"
              />
              <FormControlLabel
                value="end"
                control={
                  <Checkbox
                    checked
                    sx={{
                      color: "success.main",
                      "&.Mui-checked": {
                        color: "success.main",
                      },
                    }}
                  />
                }
                label="Success"
                labelPlacement="end"
              />

              <FormControlLabel
                value="end"
                control={
                  <Checkbox
                    checked
                    sx={{
                      color: "error.main",
                      "&.Mui-checked": {
                        color: "error.main",
                      },
                    }}
                  />
                }
                label="Danger"
                labelPlacement="end"
              />

              <FormControlLabel
                value="end"
                control={
                  <Checkbox
                    checked
                    sx={{
                      color: "warning.main",
                      "&.Mui-checked": {
                        color: "warning.main",
                      },
                    }}
                  />
                }
                label="Warning"
                labelPlacement="end"
              />
            </Box>
          </BaseCard>
        </Grid>
      </Grid>
    </Box>
  );
};

export default ExCheckbox;
